import moment from "moment";

export default {
  layout: "centered",
  head() {
    const title =
        this.settings[this.params?.url]?.name || "Redirecting to homepage",
      meta = [
        {
          hid: "og:title",
          name: "og:title",
          content: title,
        },
        {
          hid: "og:description",
          name: "og:description",
          content:
            this.settings[this.params?.url]?.description ||
            "Yönlendiriliyor.",
        },
        {
          name: "og:image",
          content: this.settings[this.params?.url]?.image,
        },
        {
          hid: "description",
          name: "description",
          content:
            this.settings[this.params?.url]?.description ||
            "Yönlendiriliyor.",
        },
        { name: "premid-details", content: "Redirecting to:" },
        {
          name: "premid-state",
          content: title,
        },
      ],
      link = [
        {
          rel: "canonical",
          href: `https://mehmetali345.xyz/redirect/${this.params.url}`,
        },
      ];

    return {
      title,
      meta,
      link,
    };
  },
  data() {
    return {
      params: this.$route.params,
      settings: {
        "codepad": {
          name: "Codepad",
          description:
            "Hafif ve Hızlı bir kod editörü.",
          url: "https://github.com/mehmetali345/codepad",

        },
      },
    };
  },
  data() {
    return {
      params: this.$route.params,
      settings: {
        "dcutils": {
          name: "Discord Utilities",
          description:
            "Discord için araçlar ve iyileştirmeler.",
          url: "https://github.com/mehmetali345/dcutilities",
        },
      },
    };
  },
  beforeDestroy() {
    clearTimeout(this.timeout);
  },
  mounted() {
    this.timeout = setTimeout(() => {
      window.location.href = this.settings[this.params?.url]?.url || "/";
    }, 5000);
  },
};
